import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../Images/IconPack";
import TextField from "@material-ui/core/TextField/TextField";
import InputLabel from "@material-ui/core/InputLabel/InputLabel";



const CorporateInvite = (props) => {

    const [login, setLoginValue] = React.useState('CU847392002');
    const [password, setPasswordValue] = React.useState('Hs@351!saS!@');
    const [nickname, setNicknameValue] = React.useState('Valeron');
    const [telegram, setTelegramValue] = React.useState('Valeron.Valeron');
    const [comment, setCommentValue] = React.useState('Valera Old Farmer');

    let rowItem = (<>
        <TableRow className="inputs-row">
            <TableCell className="padding-sides-10 color-white">
                <TextField
                    fullWidth
                    value={login}
                    onChange={(e)=>{setLoginValue(e.target.value)}}
                    variant="filled"

                />
            </TableCell>
            <TableCell className="padding-sides-10 color-white">
                <TextField
                    fullWidth
                    value={login}
                    onChange={(e)=>{setLoginValue(e.target.value)}}
                    variant="filled"
                    disabled
                />
            </TableCell>
            <TableCell className="padding-sides-10 color-white">
                <TextField
                    fullWidth
                    value={password}
                    onChange={(e)=>{setPasswordValue(e.target.value)}}
                    variant="filled"
                />
            </TableCell>
            <TableCell className="padding-sides-10 color-white">
                <TextField
                    fullWidth
                    value={nickname}
                    onChange={(e)=>{setNicknameValue(e.target.value)}}
                    variant="filled"
                />
            </TableCell>
            <TableCell className="padding-sides-10 color-grey">
                <TextField
                    fullWidth
                    value={telegram}
                    onChange={(e)=>{setTelegramValue(e.target.value)}}
                    variant="filled"
                />
            </TableCell>
            <TableCell className="padding-sides-10 color-grey">
                <TextField
                    fullWidth
                    value={comment}
                    onChange={(e)=>{setCommentValue(e.target.value)}}
                    variant="filled"
                    className="w80p"
                />
                <div className="generateTable__btnsWrapper">
                    <div className="btn--table">{IconPack.buttons.minus}</div>
                    <div className="btn--table">{IconPack.buttons.plus}</div>
                </div>
            </TableCell>
        </TableRow>

    </>);


    return (
        <div className="ÑustomStyle">
            <div className="alertPanel">
                <p className="color-white">The users listed below will be invited to join your license. You can only invite users with whom you have an agreement on migration.</p>
                <span className="btn-text color-blue">MORE</span>
            </div>
            <Table className="configTable">
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-20 ">Current login</TableCell>
                        <TableCell className="padding-sides-10 ">New login</TableCell>
                        <TableCell className="padding-sides-10 ">Password</TableCell>
                        <TableCell className="padding-sides-10">Nickname</TableCell>
                        <TableCell className="padding-sides-10">Telegram</TableCell>
                        <TableCell className="padding-sides-10 w500">Comment(role or more...)</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rowItem}
                    {rowItem}
                    {rowItem}
                    {rowItem}
                </TableBody>
            </Table>
        </div>
    );
}
export default CorporateInvite;